*{
  box-sizing: border-box;
}

body {
  margin: 0;
  background-color: #1C1C1C;
  font-family: 'Karla', sans-serif;
  
}

.mainDiv {
  width: 550px;
  height: 550px;
  background: #1F2937;
  margin: auto; 
  border-radius: 0.5em;

  
}

h1 {
  
  font-weight: 800;
  font-size: 40px;
  line-height: 40px;
  color: white;
  margin: 0;

}

span {
  color: #4ADF86;
}

p {
  
  font-weight: 400;
  font-size: 20px;
  line-height: 28px;
  color: #D5D4D8;
}

.passwordButton {
  font-family: 'Karla', sans-serif;
  font-weight: 500;
  padding: 0.5em 1em;
  font-size: 16px;
  line-height: 24px;
  color: white;
  background: #10B981;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
  border-radius: 6px;
  cursor: pointer;
  display: block;
  margin-bottom: 1em;
}

main {
  padding: 2em;
  margin: 5em 1.2em;
}

.firstPassword {
  padding: 0.5em 1em;
  margin-right: 4em;
  background: #273549;
  color: #55F991;
  font-weight: 500;
  font-size: 16px;
  line-height: 32px;
  border-radius: 0.5em;

}

.secondPassword {
  padding: 0.5em 1em;
  background: #273549;
  color: #55F991;
  font-weight: 500;
  font-size: 16px;
  line-height: 32px;
  border-radius: 0.5em;
}

.themeButton {
  font-family: 'Karla', sans-serif;
  font-weight: 500;
  padding: 0.5em 1em;
  font-size: 14px;
  line-height: 24px;
  color: white;
  background: #73A9AD;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
  border-radius: 6px;
  cursor: pointer;
  display: block;
  margin-bottom: 2em;
  float: right;
  
}

.main-inner-div {
  margin: 5em auto;
}

button:hover {
  background-color: #515E63;
}

.copyButtonDiv {
  display: flex;
  margin-top: 1em;
  justify-content: space-between;
}

.passwordsDiv {
  display: flex;
  justify-content: space-between;

}

@media(max-width:600px){

  .mainDiv{
    width: 100%;
    height: 100%;
  }
}